@import "../../../../bootstrap";

.pop-styles-card {
    background-color: $golden-60;
    overflow: hidden;
    transition: all $duration_fast;

    @media ($breakpoint_sm_max) {
        $width: 270px;
        width: $width !important;
        height: #{$width * 1.3} !important; }


    @media ($breakpoint_md) {
        $width: 270px;
        width: $width !important;
        height: #{$width * 1.3} !important; }

    @media ($breakpoint_xl) {
        width: calc(100vw / 5 - 8px) !important;
        height: calc((100vw / 5 - 8px) * 1.3) !important; }



    & img {
        transition: all $duration_fast; }

    &:hover img {
        transform: scale(1.1); }



    & .overlay {
        transition: all $duration_fast;
        opacity: 0; }

    &:hover .overlay {
        opacity: 1; } }
